@import "./../colors";

.teaching-section-two {
  padding: 8em 0;

  .teaching-section-two-title {
    margin-bottom: 2em;
    text-align: center;
  }

  .alice-carousel__stage {
    text-align: center;
  }

  .alice-carousel__stage {
    cursor: grab;
  }

  .alice-carousel__stage-item img {
    cursor: pointer;
  }

  .alice-carousel__prev-btn-item,
  .alice-carousel__next-btn-item {
    color: $lightyellow;
    font-size: 2em;
    &:hover {
      color: $ctaColor;
    }
  }

  .alice-carousel__dots-item.__active {
    background: $lightyellow !important; /* Farbe des aktiven Punkts */
  }

  .alice-carousel__dots-item {
    background: lightyellow !important; /* Farbe der inaktiven Punkte */
  }
}

.teaching-section-three-h1 {
  font-size: 3em;
}

.teaching-section-four {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8em 0;
}

@media (min-width: 768px) {
  .teaching-section-three-h1 {
    font-size: 5.653em;
  }
}
