  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5.12em;

  .webdev-section-one-desc-container {
    max-width: 33.063em;
    display: flex;
    flex-direction: column;
    gap: 1em;
  }
}

