@import "./../colors";

.webdev-section-two-content {
  padding: 8em 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .webdev-contact-form {
    box-shadow: 13px 13px 33px 0px rgba(5, 13, 16, 0.9),
      -13px -13px 26px 0px rgba(9, 27, 32, 0.9),
      13px -13px 26px 0px rgba(5, 13, 16, 0.2),
      -13px 13px 26px 0px rgba(5, 13, 16, 0.2),
      -1px -1px 2px 0px rgba(5, 13, 16, 0.5) inset,
      1px 1px 2px 0px rgba(9, 27, 32, 0.3) inset;
  }
  .webdev-section-two-monster {
    height: 25em;
    width: auto;
    transition: all 1s;
    display: none;
  }
}

@media (min-width: 576px) {
}

@media (min-width: 768px) {
  .webdev-section-two-content {
    .webdev-section-two-monster {
      margin-right: -25.25em;
      display: block;
    }
  }
}

