@import "./../../colors";

.footer-sides-container {
  display: flex;
  justify-content: space-between;
  padding: 3em;

  .copyright-container {
    display: flex;
    gap: 0.4em;
  }

  .footer-hearth {
    fill: $ctaColor;
  }

  .footer-right {
    display: flex;
    gap: 1em;

    .footer-social-icon {
      fill: $fontColor;
    }
  }
}
