@import "./../../colors";

.form-content-container {
  width: 90%;
  background: $backgroundColor;
  padding: 2em;
  border-radius: 1.25em;

  .form-titles {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.2em;
    margin-bottom: 2em;
  }

  .inputs-container {
    display: flex;
    flex-direction: column;
    gap: 0.6em;
    margin-bottom: 2em;
  }

  .sendbutton-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2em;
  }
}

@media (min-width: 768px) {
  .form-content-container {
    width: 60%;
  }
}

@media (min-width: 992px) {
  .form-content-container {
    padding: 4em;
  }
}

@media (min-width: 1200px) {
  .form-content-container {
    padding: 4em;
    width: 50%;
  }
}

@media (min-width: 1400px) {
  .form-content-container {
    width: 45%;
  }
}
