@import "./../colors";

.pluginItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1em;

  .img-container {
    background-color: $lightBlack;
    border-radius: 50%;
    padding: 1.2em;
    width: 150px;
    height: 150px;
    overflow: hidden;
    transition: transform 1s ease;

    img {
      width: 100%;
      transition: transform 1s ease;
    }

    &:hover img {
      transform: scale(
        1.2
      ); /* Hier wird der Zoom-Effekt erzeugt. Du kannst den Wert anpassen, um den gewÃ¼nschten Zoom-Grad zu erzielen */
    }
  }
}
