@import "./../../../colors";

.sectionTwoContainer {
  padding: 8em;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  justify-content: center;
  gap: 8em;
}

@media (min-width: 992px) {
  .sectionTwoContainer {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1400px) {
  .sectionTwoContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}
