.glitch-wrapper {
  width: 100%;
  height: 100%;
}

.glitch {
  position: relative;
  font-size: 120px;
  font-weight: 700;
  line-height: 1.2;
  color: #fff;
  letter-spacing: 8px;
  z-index: 1;
}

.glitch:before {
  content: attr(data-glitch);
  position: absolute;
  top: 0;
  left: -2px;
  width: 100%;
  color: #fff;
  overflow: hidden;
  clip: rect(0, 900px, 0, 0);
  -webkit-animation: noise-before 18s infinite;
  animation: noise-before 18s infinite;
}

.glitch:after {
  content: attr(data-glitch);
  position: absolute;
  top: 0;
  left: 2px;
  width: 100%;
  color: #fff;
  overflow: hidden;
  clip: rect(0, 900px, 0, 0);
  -webkit-animation: noise-after 17s infinite;
  animation: noise-after 17s infinite;
}

