@import "./../colors";

.CategoryBox {
  margin: auto;
  cursor: pointer;
  border-radius: 1.25em;
  width: 18.75em;
  height: 18.75em;
  row-gap: 1em;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 0px #050f12, 0px 0px 0px #09191e,
    inset 14px 14px 0px #050f12, inset -14px -14px 0px #09191e;
  border: 5px solid #03080a;
  transition: all 0.5s;

  &:hover {
    transform: scale(102%);
    box-shadow: 14px 14px 0px #050f12, -14px -14px 0px #09191e,
      inset 0px 0px 0px #050f12, inset 0px 0px 0px #09191e;
  }

  .backgroundLayer {
    position: absolute;
    background-color: $backgroundLayerItemColor;
    border-radius: 20px;
    height: 17.5em;
    width: 17.5em;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .CategoryBoxContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 1em;
    color: $fontColor;
  }
}

.background {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#cb1 {
  .background {
    background: url(./../../public/BoxBG1.webp);
    background-position: center;
    transition: all 2s linear;
  }
}

#cb2 {
  .background {
    background: url(./../../public/BoxBG2.webp);
    background-position: left 30%;
    transition: all 2s linear;
  }
}

#cb3 {
  .background {
    background: url(./../../public/BoxBG3.webp);
    background-position: right 60%;
    transition: all 2s linear;
  }
}

.CategoryBox {
  &:hover {
    .background {
      transform: scale(1.5);
    }
  }
}
