@import "./../../colors";

.hero-section-subpage {
  padding: 8em 0;
  display: flex;
  align-items: center;
  justify-content: center;

  .hero-section-subpage-title {
    font-size: 2em;
    text-align: center;
    overflow: hidden;
  }
}

@media (min-width: 576px) {
}

@media (min-width: 768px) {
  .hero-section-subpage {
    .hero-section-subpage-title {
      font-size: 4em;
    }
  }
}

@media (min-width: 992px) {
  .hero-section-subpage {
    .hero-section-subpage-title {
      font-size: 5em;
    }
  }
}

@media (min-width: 1200px) {
}

@media (min-width: 1400px) {
  .hero-section-subpage {
    .hero-section-subpage-title {
      font-size: 5.653em;
    }
  }
}
