@import "./../colors";

.contact-section-two {
  width: 100%;
  height: 90vh;
  position: relative;
  overflow: hidden;
}

.contact-section-two-background {
  height: 100%;
  width: 100%;
  background-color: red;
  position: relative;
  background: url(../../public/contact-bg.webp);
  top: 0;
}

.contact-section-two-background-layer {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  background: $backgroundLayerColor;
  display: flex;
  justify-content: center;
  align-items: center;

  .form-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media (min-width: 992px) {
  .contact-section-two {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
  }
}
