@import "./../../colors";

@-webkit-keyframes scale {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes scale {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.herosection {
  width: 100%;
  height: 100vh;
  overflow: hidden;

  .background {
    height: 100%;
    animation: scale 30s infinite;
  }

  .herosection-background-layer {
    height: 100vh;
    width: 100%;
    background-color: $backgroundLayerColor;
    position: absolute;
    top: 0;

    .herosection-content {
      height: 100vh;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;

      .herosection-title-container {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        gap: 1em;

        .herosection-big-title {
          text-align: center;
          font-size: 2.1em;
        }

        .herosection-title {
          font-size: 1.4em;
        }
      }

      .herosection-cta-container {
        margin-top: 128px;

        .devc-icon-btn-blue {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-pack: center;
          -ms-flex-pack: center;
          justify-content: center;
          -webkit-box-align: center;
          -ms-flex-align: center;
          align-items: center;
          -webkit-column-gap: 1em;
          -moz-column-gap: 1em;
          column-gap: 1em;
          text-decoration: none;
          color: $fontColor;

          .herosection-cta-svg {
            /* Button Drop */
            -webkit-box-shadow: 0.27405187487602234px 0.30145707726478577px
                0.8148148059844971px 0px rgba(0, 0, 0, 0.39),
              1.295517921447754px 1.425069808959961px 3.185185194015503px 0px
                rgba(0, 0, 0, 0.61),
              3.3633639812469482px 3.699700355529785px 10px 0px #000;
            box-shadow: 0.27405187487602234px 0.30145707726478577px
                0.8148148059844971px 0px rgba(0, 0, 0, 0.39),
              1.295517921447754px 1.425069808959961px 3.185185194015503px 0px
                rgba(0, 0, 0, 0.61),
              3.3633639812469482px 3.699700355529785px 10px 0px #000;
          }
        }
      }
    }
  }
}

@media (min-width: 576px) {
  .herosection {
    .herosection-background-layer {
      .herosection-content {
        .herosection-title-container {
          .herosection-big-title {
            font-size: 4em;
          }

          .home-herosection-title {
            font-size: 1.8em;
          }
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .herosection {
    .herosection-background-layer {
      .herosection-content {
        .herosection-title-container {
          -webkit-box-align: start;
          -ms-flex-align: start;
          align-items: start;
          .herosection-big-title {
            text-align: start;
          }
        }
      }
    }
  }
}

@media (min-width: 992px) {
  .herosection {
    .herosection-background-layer {
      .herosection-content {
        .herosection-title-container {
          .herosection-big-title {
            font-size: 5.653rem;
          }

          .herosection-title {
            font-size: 1.999rem;
          }
        }
      }
    }
  }
}
