@import "./_colors.scss";

@font-face {
  font-family: "Athomsphaeric";
  src: url("./../public/fonts/aAtmospheric.ttf") format("truetype");
}
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1em;
  font-weight: 400;
  color: $fontColor;
  background-color: $backgroundColor;
}

a {
  text-decoration: none;
  &:visited {
    color: inherit;
  }
}

textarea {
  font-family: "Rubik", sans-serif;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 400;
}

h1 {
  font-family: "Athomsphaeric", Arial, Helvetica, sans-serif;
  margin-top: 0;
  font-size: 5.653rem;
  line-height: 1em;
}

.footer-main-title {
  font-family: "Athomsphaeric", Arial, Helvetica, sans-serif;
}

h2 {
  font-size: 3.998rem;
}

h3 {
  font-size: 2.827rem;
}

h4 {
  font-size: 1.999rem;
}

h5 {
  font-size: 1.414rem;
}

p {
  font-weight: 100;
}

.pH4 {
  font-size: 1.999rem;
}

small,
.text_small {
  font-size: 0.707rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background1 {
  background: url("./herosection-bg.webp");
}

.background2 {
  background: url("./herosection-bg2.webp");
}

.background3 {
  background: url("./herosection-bg3.webp");
}

.background4 {
  background: url("./herosection-bg4.webp");
}

.text-span {
  color: $secondColor;
}

.text-span-brown {
  color: $lightbrown;
}

.text-span-yellow {
  color: $lightyellow;
}

.fill-blue {
  fill: $secondColor;
  border-radius: 20px;
}

.fill-brown {
  fill: $lightbrown;
  border-radius: 20px;
}

.fill-white {
  fill: $fontColor;
  border-radius: 20px;
}

.fill-yellow {
  fill: $lightyellow;
  border-radius: 20px;
}

.svg16 {
  height: 1em;
  width: 1em;
}

.svg24 {
  height: 1.5em;
  width: 1.5em;
}

.svg32 {
  height: 2em;
  width: 2em;
}

.svg46 {
  height: 2.875em;
  width: 2.875em;
}

.svg64 {
  height: 4em;
  width: 4em;
}

.svg80 {
  height: 5em;
  width: 5em;
}

.svg150 {
  height: 9.375em;
  width: 9.375em;
}

.svgDCI {
  width: 20.4em;
  height: auto;
}

.mb {
  margin-bottom: 0.3em;
}

.ml {
  margin-left: 0.1em;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }

  .svgDCI {
    width: 29.4em;
    height: 13.8em;
  }
}

@media (min-width: 768px) {
  html {
    font-size: 1.1rem; /* 24px */
  }
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  html {
    font-size: 1.125rem; /* 24px */
  }
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  html {
    font-size: 1.15rem; /* 24px */
  }
  .container {
    max-width: 1140px;
  }
}

@media (min-width: 1400px) {
  html {
    font-size: 1.2rem; /* 32px */
  }
  .container {
    max-width: 1320px;
  }
}
